/**
 * 备注面板
 * visible 是否显示，支持 .sync 修饰符
 * content 内容
 * maxLength 内容的最大长度
 * @confirm  确认事件
 * @close 关闭事件
 * */
export default {
	name: 'remark-panel',
	props: {
		visible: {
			type: Boolean,
			default: false
		},
		content: {
			type: String,
			default: ''
		},
		maxLength: {
			type: Number,
			default: 30
		}
	},
	data() {
		return {
			fontNumber: 0,
			remark: '',
			isRemarkFocus: true
		};
	},
	computed: {
		getContentLength() {
			return this.remark !== '' ? this.remark.length : this.content.length;
		}
	},
	methods: {
		handleCancelRemark() {
			this.isRemarkFocus = false;
			this.remark = '';
			this.fontNumber = 0;
			this.$emit('update:visible', false);
			this.$emit('close');
		},
		handleConfirmRemark() {
			this.$emit('confirm', this.remark);
			this.handleCancelRemark();
		},
		handleInputRemark(e) {
			const remark = e.detail.value;
			if (this.maxLength < remark.length) {
				return;
			}
			this.fontNumber = remark.length;
			this.remark = remark;
		}
	}
};
